import React, { useState, useEffect, useCallback } from 'react';

interface ToastProps {
  type: 'success' | 'warning' | 'error' | 'info';
  title: string;
  message: string;
  duration?: number;
  onClose: () => void;
}

interface ToastContainerProps {
  children: React.ReactNode;
}

